@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@800&display=swap);
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
  }

body {
    background: var(--color-bg);
  }
  
  a {
    color: unset;
    text-decoration: none;
  }

  
  
  .gradient__bg {

      /* ff 3.6+ */ 
  
      /* safari 5.1+,chrome 10+ */
  
      /* opera 11.10+ */
      background:-o-linear-gradient(90deg, rgba(255, 255, 255, 1) 0%, rgba(174, 180, 255, 1) 77%, rgba(139, 145, 255, 1) 100%);
  
      /* ie 6-9 */
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#8B91FF', endColorstr='#FFFFFF', GradientType=0 );
  
      /* ie 10+ */
  
      /* global 94%+ browsers support */
      background:linear-gradient(90deg, rgba(255, 255, 255, 1) 0%, rgba(174, 180, 255, 1) 77%, rgba(139, 145, 255, 1) 100%);
  
  }
  
  
  .gradient__text {
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  .section__padding {
    padding: 4rem 6rem;
  }
  
  .section__margin {
    margin: 4rem 6rem;
  }
  
  .scale-up-center {
      animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
  }
  
  @keyframes scale-up-center {
    0% {
      transform: scale(0.5);
    }
    100% {
      transform: scale(1);
    }
  }
  
  @media screen and (max-width: 700px) {
    .section__padding {
      padding: 4rem;
    }
  
    .section__margin {
      margin: 4rem;
    }
  }
  
  @media screen and (max-width: 550px) {
    .section__padding {
      padding: 4rem 2rem;
    }
  
    .section__margin {
      margin: 4rem 2rem;
    }  
  }
* {
    box-sizing: border-box;
}

.header{
    display: grid;
    padding: 20px;
    background: var(--gradient-text-h1);
    justify-items: center;
}

.header h1{
    font-family: var(--font-family);
    color: #FFF;
}
.footer{
    width: 100%;
    height: 100px;
    background: #000;
}
* {
    box-sizing: border-box;
}

.Slides{
    display: flex;
    padding: 20px;
    grid-template-columns: repeat(4, 100%);
    margin-left: 10%;
    grid-column-gap: 10px;
    
}


.Slides-Card {
    display: grid;
    position: relative;
    width: 20%;
    height: 360px;
    background: #fff;
    box-shadow: 0 2px 2px 0 rgba(0,0,0,0.2);
    display: grid;
    

}

.Slides-Card img {
    width: 100%;
    height: auto;
    padding: 5%;
}


.imgfront, .imgback {
    position: absolute;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  
.imgback {
    opacity: 0;
    transition: .3s ease;
}
.Slides-Card:hover .imgback {
    opacity: 1;
    
}
.Slides-Card:hover .imgfront {
    opacity: 1;
    
}
.Slides-Card-container {
    display: grid;
    position: absolute;
    bottom: 10px;
    text-align: center;
    font-family: var(--font-family);
    left: 20%;
    bottom: 10%;
}

.Slides-Card-container h4{
    color: #AE67FA;
   
}

.socialMedia{
    display: flex;  
    width: 100%;
    height: 80px;
    justify-content: center;
    background: var(--gradient-text);
  
}

.socialMedia-container img{
    margin-left: 30px;
    margin-right: 30px;
    margin-top: 4px;
    width: 60px;
    height: 60px;
    color: red;
}
.navbar {
    display: flex;
    align-items: center;
    padding: 2rem 6rem;
    
    background: #FFF;

}

.navbar-links {
    flex: 1 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar-links-container {
    flex: 1 1;
    display: flex;
    margin-right: 2;
    margin-left: 2;
    justify-content: space-between;
    align-items: center;
}
.navbar-links-container img {
    width: 62px;
    height:62px;
} 

.navbar-links-container p {
    font-family: var(--font-family);
    font-size: 20px;
    color: #000;
}

.navbar-menu {
    flex: 1 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.navbar-menu img {
    width: 62px;
    height:62px;
}


.navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}


.navbar-menu-container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    
    text-align: end;
    background: var(--gradient-text);
    padding: 2rem;
    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 60px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

.navbar-menu-container p {
    margin: 1rem 0;
    color: #FFF;
    font-family: var(--font-family);
}


@media screen and (max-width: 1050px) {
    .navbar-links-container {
        display: none;
    }

    .navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .navbar {
        padding: 2rem 4rem;
    }
}

.welcome {
    width: 100%;
    height: auto;
    position: relative;
}

.welcome-container img{
    width: 100%;
    height: auto;
}

.gradient-text {
    /* Fallback: Set a background color. */
    background-color: red;
    
    /* Create the gradient. */
    background-image: linear-gradient(45deg, #f3ec78, #af4261);
    
    /* Set the background size and repeat properties. */
    background-size: 100%;
    background-repeat: repeat;
  
    /* Use the text as a mask for the background. */
    /* This will show the gradient as a text color rather than element bg. */
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-background-clip: text;
    -moz-text-fill-color: transparent;
  }
  

.welcome-container-text h1{
    font-size: 50px;
    position: absolute;
    margin-left: 15%;
    margin-top: 15%;
    color: linear-gradient(to right, #E6E6FA 0%, #330867 100%);
    font-family: var(--font-family);
    
}

:root {
  --font-family: 'Raleway', sans-serif;
  --gradient-text-h1: linear-gradient(to right, #E6E6FA 0%, #330867 100%);
  --gradient-text: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
  --gradient-bar: linear-gradient(103.22deg, #a2ca6d -13.86%, #df2ba9 99.55%);
  
  --color-bg: #FFF;
  --color-footer : #E6E6FA;
  --color-blog: #042c54;
  --color-text: #000;
  --color-subtext: #6A5ACD;
}
